.product {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 30px 0px;
}

.product__item {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-bottom: 1rem;
  max-width: 370px;
  padding: 1rem;
}

.product__item--link {
  color: #171919;
  text-decoration: none;
  display: block;
  height: 100%;
  margin: auto;
  background: #ffffff;
  padding: 0 0 1rem 0;
}

.product__item__info {
  max-width: 350px;
  width: 90%;
  display: block;
  margin: auto;
  padding: 35px 0px;
}

.product__item__size {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 15px;
}

.product__item__info--successCart,
.product__item__info--errorSize {
  font-size: 1.3rem;
  color: #006400;
  font-weight: 600;
  text-align: center;
  margin-top: 18px;
  display: none;
  -webkit-transition: 0.1s all;
  -o-transition: 0.1s all;
  transition: 0.1s all;
}

.product__item__info--errorSize {
  color: #cf3838;
}

.product__item__info--ok {
  display: block;
}

.product__item__size p {
  font-size: 1.4rem;
  width: 40px;
  height: 40px;
  vertical-align: middle;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border: 1px #171919 solid;
  color: #171919;
  background-color: transparent;
  border-radius: 50%;
  cursor: pointer;
  margin: 0 5px;
  -webkit-transition: 0.1s all;
  -o-transition: 0.1s all;
  transition: 0.1s all;
}

.product__item__size p.active {
  color: #ffffff;
  background-color: #171919;
}

.product__item__size p:hover {
  color: #ffffff;
  background-color: #171919;
}

.product__item--image {
  position: relative;
  width: 100%;
}

.product__item--image img::before {
  background-image: url(https://amaro.com/webapp/media/background-image.png);
  bottom: 0;
  content: "";
  left: 0;
  opacity: 0.2;
  position: absolute;
  right: 0;
  top: 0;
}

.product__item--image img {
  width: 100%;
}

.product__item--name h1 {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  font-family: Gotham, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 1.4rem;
  font-weight: 400;
  margin: 2rem 0 1rem 0;
  padding: 0 2rem;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.product__item--price p {
  font-size: 1.25rem;
  margin: 0;
  padding: 0;
  text-align: center;
  line-height: 1.6;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-direction: row;
      flex-direction: row;
}

.product__item--price p span:first-child {
  padding-right: 15px;
}

.product__item--price p span:last-child {
  color: #999999;
}

.product__item--price--discount p:last-child span:first-child {
  text-decoration: line-through;
  color: #999999;
}

.product__item--price--discount p:last-child span:last-child {
  color: #cf3838;
  font-weight: 600;
}

.product__item__shop {
}

.product__item__shop button {
  max-width: 250px;
  width: 90%;
  margin: 20px auto 0 auto;
  display: block;
  background-color: #171919;
  border: 1px #171919 solid;
  color: #ffffff;
  text-transform: uppercase;
  padding: 1.4rem 0;
  font-size: 1.5rem;
  cursor: pointer;
  -webkit-transition: 0.05s all;
  -o-transition: 0.05s all;
  transition: 0.05s all;
}

.product__item__shop button:hover,
.product__item__shop button:active,
.product__item__shop button:focus,
.product__item__shop button:visited {
  background-color: #d0d0d0;
  border: 1px #d0d0d0 solid;
}

@media screen and (min-width: 1200px) {
  .product__item {
    width: 20%;
  }
}

@media screen and (max-width: 1199px) {
  .product__item {
    width: 25%;
  }
}

@media screen and (max-width: 1023px) {
  .product__item {
    width: 33.33333%;
  }
}

@media screen and (max-width: 768px) {
  .product__item {
    width: 50%;
    padding: 0.3rem;
  }

  .product__item--name h1 {
    font-size: 1.2rem;
  }

  .product__item--price p span:first-child {
    padding-right: 0px;
  }
}



